.App {
  position: relative;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: -webkit-max-content max-content 1fr;
  grid-template-rows: max-content max-content 1fr;
  grid-template-areas:
    "head head"
    "lefttitle  righttitle"
    "leftcontent  rightcontent";
  width: 100%;
  height: 100%;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

html {
  position: relative;
  height: 100vh;
  width: 100vw;
}

body {
  height: 100%;
  width: 100%;
}

div#root {
  height: 100%;
  width: 100%;
}

.MainTitle {
  grid-area: head;
}
.LeftTitle {
  grid-area: lefttitle;
}
.InputCode {
  grid-area: leftcontent;
}
.RightTitle {
  grid-area: righttitle;
}
.GraphqlCodeBlock {
  grid-area: rightcontent;
  overflow-x: auto;

}



